import React from 'react';

class Card extends React.Component{
	constructor(props){
		super(props);
		this.state={

		}
	}

	render(){
		return(
			<div className='cardComponent grow'>
				<a className={this.props.name} onClick={this.props.click}>
					{this.props.source[this.props.index].image}
					<span className={this.props.name}>{this.props.name}</span>	
				</a>
			</div>
		)}
}
export default Card


