export const allowPolishO=(string)=>{
	let newString=""
	for(let i=0;i<string.length;i++){
		if(string.charAt(i)==="o"){
			newString+="ó"
		}else{
			newString+=string.charAt(i)
		
		}
	}
	return newString
}
